h4 {
  text-decoration: none;
  position: relative;
  display: block;
  padding: 16px 0;
  margin: 0 12px;
  font-size: 1.1rem;
  text-transform: uppercase;
  transition: color 0.1s, background-color 0.1s;
  color: #000;
}
h4:hover {
  color: #ab4de1;
  text-shadow: -1px 1px 10px rgba(0, 0, 0, 0.75),
}
h4:focus, h4:active {
  color: #d45f00;
}

h4::before {
  content: '';
  display: block;
  position: absolute;
  top: 100%;
  height: 3px;
  width: 100%;
  background-color: #00bcd4;
  transform-origin: center top;
  transform: scale(0, 1);
  transition: color 0.1s, transform 0.2s ease-out;
}

h4:active::before {
  background-color: #00bcd4;
}

h4:hover::before,
h4:focus::before {
  transform-origin: center top;
  transform: scale(1, 1);
}

